

















































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';

@Component
export default class CTimer extends Vue {
  // Data
  // stop | play | pause
  private dataState: string = 'stop';
  // off | up
  private dataVolume: string = 'up';
  private dataDisabled: boolean = false;
  private dataTimerId: any = 0;
  private dataBaseMoment: any = moment(new Date()).endOf('seconds');
  private dataSetMoment: any = this.dataBaseMoment.clone();
  private dataPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
  };
  private dataHours: number[] = [];
  private dataMinutesAndSeconds: number[] = [];
  private dataHour: number = 0;
  private dataMinute: number = 0;
  private dataSecond: number = 0;
  private dataShouldRing: boolean = false;

  // Watch
  @Watch('dataState')
  onStateChange(newValue: string, oldValue: string): void {
    if (this.dataState === 'stop') {
      this.dataDisabled = false;
    } else {
      this.dataDisabled = true;
    }
  }
  @Watch('dataShouldRing')
  onShouldRingChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataBaseMoment')
  onBaseMomentChange(newValue: string, oldValue: string): void {
    this.diffDatetime();
  }

  // Lifecycle hook
  created(): void {
    this.initHours();
    this.initMinutesAndSeconds();
  }

  // Methods
  private initHours(): void {
    const HOUR_TERM: number = 24;
    for (let i: number = 0; i < HOUR_TERM; i++) {
      this.dataHours.push(i);
    }
  }
  private initMinutesAndSeconds(): void {
    const MINUTE_AND_SECOND_TERM: number = 60;
    for (let i: number = 0; i < MINUTE_AND_SECOND_TERM; i++) {
      this.dataMinutesAndSeconds.push(i);
    }
  }
  private initPointDiff(): void {
    Vue.set(this.dataPointDiff, 'hour', this.dataHour);
    Vue.set(this.dataPointDiff, 'minute', this.dataMinute);
    Vue.set(this.dataPointDiff, 'second', this.dataSecond);
  }
  private resetMoment(): void {
    this.dataBaseMoment = moment(new Date()).endOf('seconds');
    this.dataSetMoment = this.dataBaseMoment.clone();
    this.dataSetMoment.add(this.dataPointDiff.hour, 'hours')
    this.dataSetMoment.add(this.dataPointDiff.minute, 'minutes')
    this.dataSetMoment.add(this.dataPointDiff.second, 'seconds')
  }
  private traceBaseMoment(): void {
    if (0 < this.dataSetMoment.diff(this.dataBaseMoment, 'seconds')) {
      this.dataTimerId = setInterval((): void => {
        let now: any = moment(new Date()).endOf('seconds');
        this.dataBaseMoment = now;
        if (this.dataSetMoment.diff(this.dataBaseMoment, 'seconds') <= 0) {
          clearInterval(this.dataTimerId);
          this.dataState = 'stop';
          this.ringChime();
        }
      }, 1000);
    } else {
      clearInterval();
      this.dataState = 'stop';
    }
  }
  private ringChime(): void {
    let ringElem: HTMLAudioElement = <HTMLAudioElement>document.getElementById('ring');
    ringElem.currentTime = 0;
    ringElem.play();
  }
  private stopBaseMoment(): void {
    clearInterval(this.dataTimerId);
  }
  private diffDatetime(): void {
    let nowMoment: any = this.dataBaseMoment.clone();
    let hourDiff: number = this.dataSetMoment.diff(nowMoment, 'hours');
    Vue.set(this.dataPointDiff, 'hour', hourDiff);
    nowMoment.add(hourDiff, 'hours');
    let minuteDiff: number = this.dataSetMoment.diff(nowMoment, 'minutes');
    Vue.set(this.dataPointDiff, 'minute', minuteDiff);
    nowMoment.add(minuteDiff, 'minutes');
    let secondDiff: number = this.dataSetMoment.diff(nowMoment, 'seconds');
    Vue.set(this.dataPointDiff, 'second', secondDiff);
  }
  private listenSetPointHourChange($event: Event): void {
    this.dataHour = Number((<HTMLInputElement>(<any>$event).target).value);
    Vue.set(this.dataPointDiff, 'hour', this.dataHour);
  }
  private listenSetPointMinuteChange($event: Event): void {
    this.dataMinute = Number((<HTMLInputElement>(<any>$event).target).value);
    Vue.set(this.dataPointDiff, 'minute', this.dataMinute);
  }
  private listenSetPointSecondChange($event: Event): void {
    this.dataSecond = Number((<HTMLInputElement>(<any>$event).target).value);
    Vue.set(this.dataPointDiff, 'second', this.dataSecond);
  }
  private listenPlay($event: Event): void {
    if (this.dataState === 'stop') {
      this.initPointDiff();
    }
    this.dataState = 'play';
    this.resetMoment();
    this.traceBaseMoment();
  }
  private listenPause($event: Event): void {
    this.dataState = 'pause';
    this.stopBaseMoment();
  }
  private listenStop($event: Event): void {
    this.dataState = 'stop';
    this.stopBaseMoment();
    this.initPointDiff();
    this.resetMoment();
  }
}
